import React from "react";
import "./AboutMe.css";
import { CalculateAge } from "../Utilities/Utilities.js";
import PropTypes from "prop-types";
import { BiCake } from "react-icons/bi";
import { GoLocation } from "react-icons/go";
import { BsPersonFill } from "react-icons/bs";
import { aboutMe } from "../../config.js";

function AboutMe({ dark_mode }) {
    const { image, cv, skills, passionateAbout, hobbies } = aboutMe;

    const { fullname, location, dateOfBirth, summary } = aboutMe;

    const technicalSkills = skills.map((item, index) => {
        return (
            <div key={index}>
                <div className="row">
                    <div className="col-sm-4 col-md-4">
                        <span>
                            <b> {item.name} </b>
                        </span>
                    </div>
                    <div className="col-sm-8 col-md-8">
                        <span> {item.tools} </span>
                    </div>
                </div>
            </div>
        );
    });

    const passionateAboutList = passionateAbout.map((item, index) => {
        return <li key={index}> {item.name} </li>;
    });

    const hobbiesList = hobbies.map((item, index) => {
        return <li key={index}> {item.name} </li>;
    });

    return (
        <div className={dark_mode ? "dark_mode_about_me" : "light_mode_about_me"}>
            <div className="container py-3" id="AboutMe">
                <div className="row">
                    <div className="col-md-6 py-3 border">
                        <div className="row justify-content-between">
                            <div className="col-sm-4 col-md-4">
                                <img src={image} className="img-fluid" alt="" />
                            </div>
                            <div className="col-sm-8 col-md-8">
                                <div className="personal">
                                    <div className="icon">
                                        <BsPersonFill />
                                    </div>
                                    <span>{fullname}</span> <br />
                                </div>
                                <div className="personal">
                                    <div className="icon">
                                        <GoLocation />
                                    </div>
                                    <span>{location}</span> <br />
                                </div>
                                <div className="personal">
                                    <div className="icon">
                                        <BiCake />
                                    </div>
                                    <span>
                                        <CalculateAge dateOfBirth={dateOfBirth} />
                                    </span>
                                    <br />
                                </div>
                            </div>
                        </div>
                        <div className="py-5">
                            <h4>
                                <b> Technical Skills </b>
                            </h4>
                            <br />
                            {technicalSkills}
                        </div>
                    </div>
                    <div className="col-md-6 py-3 border">
                        <h2>
                            <b> About me </b>
                        </h2>
                        <p>
                            <span>{summary}</span>
                        </p>
                        <p>
                            <span>
                                <b>Passionate about</b>
                            </span>
                        </p>
                        <ul>{passionateAboutList}</ul> <br />
                        <p>
                            <span>
                                <b>Hobbies</b>
                            </span>
                        </p>
                        <ul>{hobbiesList}</ul> <br />
                        <div align="center">
                            <a rel="noopener noreferrer" target="_blank" href={cv}>
                                <img className="cv" src="/images/CVLogo.png" alt="cv_logo" />
                            </a>
                            <br />
                            <p className="quote">
                                <span>
                                    <i>"Talk is cheap show me your code"</i>
                                </span>
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

AboutMe.propTypes = {
    dark_mode: PropTypes.bool.isRequired,
};

export default AboutMe;
